.fdry-main__standard-page {
	margin: 60px 0 90px;

	.fdry-main__standard-page-title {
		margin-bottom: 45px;
	}

	.fdry-main__standard-page-content {
		h2 + p {
			margin-top: 25px;
		}

		p + h3,
		ul + h3 {
			margin-top: 25px;
		}
		p + h4,
		ul + h4 {
			margin-top: 20px;
		}

		p + h2,
		ul + h2 {
			margin-top: 35px;
		}

		p + p {
			margin-top: 15px;
		}

		h3 {
			margin-bottom: 25px;
		}

		h4 {
			margin-bottom: 20px;
		}

		ul {
			list-style-position: outside;
			padding-left: 25px;
		}

		table {
			margin: 30px 0px;
			overflow: auto;

			@include media('<phone-land') {
				margin: 30px 0px;
			}
		}
	}
}

.wp-block-table.is-style-stripes td,
.wp-block-table.is-style-stripes th {
	padding: 10px 5px;
}
